.hidden {
  position:absolute;
  left:-10000px;
  top:auto;
  width:1px;
  height:1px;
  overflow:hidden;
}

.show-animation {
  opacity: 1;
}

.hide-animation {
  opacity: 0;
}

body {
  box-sizing: border-box;
}

